import { http } from "./index";
import { Message } from "element-ui";
//登录
export const login = async (data) => {
  try {
    const res = await http("/login", "POST", data);
    Message.success(res.meta.msg);
    return res;
  } catch (error) {
    return error;
  }
};
//获取左侧权限菜单
export const getMenus = async () => {
  try {
    const res = await http("/menus");
    return res.data;
  } catch (error) {
    return error;
  }
};
